$prefix: '.rise-blocks';
#{$prefix}-image-slider {

    .slick-track {
        display: flex !important;        
        align-items: center;
    }
    .image-slider {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
    }
    #{$prefix}-slider-arrow {
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        background: white;
        border: none;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        font-size: 25px;
        line-height: 1;
        z-index: 99;
        padding: 0;
        transition: all .3s;
        cursor: pointer;

        &:hover {
            background-color: #FF8500;
        }

        &.rise-blocks-next-arrow  {
            right: 10px;
        }

        &.rise-blocks-prev-arrow  {
            left: 10px;
        }
    }

    img {
        display: block;      
        max-width: 100%; 
    }

    ul.slick-dots {
        position: absolute;
        bottom: 30px;
        display: flex;
        list-style: none;
        left: 0;
        right: 0;
        margin: auto;
        justify-content: center;
        padding: 0;

        li {
            padding: 5px;
            button {
                border: none;
                background: #fff;
                height: 20px;
                width: 20px;
                border-radius: 50%;
                font-size: 0;
                cursor: pointer;
                padding: 0;
            }

            &.slick-active,
            &:hover {
                button {
                    background-color: #FF8500;
                }
            }
        }
    }
}

